import React from "react";

const UserTable = (props)=>(
	<table className="table">
		<thead>
      		<tr>
        		<th>Name</th>
        		<th>Username</th>
        		<th>Actions</th>
      		</tr>
    	</thead>
    	<tbody>
      		{props.users.length > 0 ? (
      			props.users.map((user) => (
      				<tr key={user.id}>
        				<td>{user.name}</td>
        				<td>{user.username}</td>
        				<td>
          					<button className="btn btn-primary" onClick={() => props.editUserRow(user)}>Edit</button>
          					<button className="btn btn-primary" onClick={() => props.deleteUser(user.id)}>Delete</button>
        				</td>
      				</tr>
      			))
      		) : (
      			<tr>
      				<td>NO USERS</td>
      			</tr>
      		)}
    	</tbody>
	</table>
)

export default UserTable;