import React, {useState, useEffect} from "react";

const UpdateUserForm = (props) => {
	const [user, setUser] = useState(props.currentUser);

	const handleInputChange = (event) => {
		const {name, value} = event.target;

		setUser({...user, [name]: value});
	}

	useEffect(() => {
		setUser(props.currentUser)
	}, [props])

	return (
		<form onSubmit={(event) => {
			event.preventDefault();
			props.updateUser(user.id, user);
		}}>

			<div className="form-group">
				<label>Name</label>
				<input type="text" name="name" value={user.name} className="form-control" onChange={handleInputChange}/>
			</div>
			<div className="form-group">
				<label>Username</label>
				<input type="text" name="username" value={user.username} className="form-control" onChange={handleInputChange}/>
			</div>
			<button className="btn btn-primary">Update User</button>
			<button onClick={() => props.setEditing(false)} className="btn btn-primary">Cancel</button>
		</form>
	);
}

export default UpdateUserForm;