import React, {useState} from 'react';
import UserTable from './Table';
import AddUserForm from './AddUserForm';
import UpdateUserForm from './UpdateUserForm';

const App = () => {
  const usersData = [
    { id: 1, name: 'Anuj', username: 'kamat1769' },
    { id: 2, name: 'Kidrah', username: 'hardikbansal' },
    { id: 3, name: 'ghost', username: 'mohitochan' }
  ];

  const INITIALEMPTYSTATE = {
    id: undefined,
    name: "",
    username: ""
  }

  const [users, setUsers] = useState(usersData); 
  const [editing, setEditing] = useState(false);
  const [currentUser, setCurrentUser] = useState(INITIALEMPTYSTATE)

  const addUser = (user) => {
    user.id = users.length + 1;
    setUsers([...users, user]);
  }

  const deleteUser = (id) => {
    setEditing(false);
    setUsers(users.filter((user) => user.id !== id));
  }

  const editUserRow = (user) => {
    setEditing(true);

    setCurrentUser({id: user.id, name: user.name, username: user.username})
  }

  const updateUser = (id, updatedUser) => {
    setEditing(false);

    setUsers(users.map((user) => (user.id === id ? updatedUser : user)))
  }

  return (
    <div>
      <h1 className="text-center">CRUD App with hooks</h1>
      
      <div className="row">
        {editing ? (
          <div className="col-xs-6">
            <h2>Update User</h2>
            <UpdateUserForm currentUser={currentUser} setEditing={setEditing} updateUser={updateUser}/>
          </div>
        ) : (
          <div className="col-xs-6">
            <h2>Add User</h2>
            <AddUserForm addUser={addUser}/>
          </div>
        )}

        <div className="col-xs-6">
          <h2>View Users</h2>
          <UserTable users={users} deleteUser={deleteUser} editUserRow={editUserRow}/>
        </div>
      </div>
    </div>
  )
}

export default App
